// @flow

import React from 'react';
import { Link } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';
import { Absolute, Toolbar, NavLink } from 'rebass';

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateX(-15px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
`;

const growLeft = keyframes`
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
`;

const FadeLinkLeft = styled(NavLink)`
  opacity: 0;
  animation: ${fadeIn} ease 0.4s forwards;
`;

const FadeLinkRight = styled(NavLink)`
  opacity: 0;
  animation: ${fadeIn} ease 0.4s forwards;
  animation-delay: 0.8s;
`;

const GrowLine = styled.hr`
  border-bottom: 2px #79e4a6 solid;
  width: 60%;
  transform: scaleX(0);
  transform-origin: left;
  animation: ${growLeft} cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s forwards;
  animation-delay: 0.4s;
`;

const NavBar = () => {
  return (
    <div>
      <Absolute z={2} top left right>
        <Toolbar w={'100%'} bg="transparent" color="teal3">
          <FadeLinkLeft href="/">Home</FadeLinkLeft>
          <FadeLinkLeft href="/about">About</FadeLinkLeft>
          <GrowLine />
          <FadeLinkRight
            ml="auto"
            mr={[2, 3]}
            href="http://adamfriedl.com"
            children="by Adam Friedl"
            style={{ fontFamily: 'Damion' }}
          />
        </Toolbar>
      </Absolute>
    </div>
  );
};
export default NavBar;



